<template>
  <div class="new-order-parts">
    <div v-for="(item, index) in reverse || []" :key="index" class="box" @click="goOrder(item)">
      <div class="header-img">
        <van-image
          class="order-img"
          height="3.6rem"
          width="100%"
          fit="cover"
          lazy-load
          :src="item.imageUrl | imageToHttps | imageToWebp($webpSurpport)"
        />
        <div class="count-down-tag">
          <van-icon name="clock-o" color="#fff" />
          <van-count-down class="time" :time="item.reserveEndTime - Date.parse(new Date())" format="DD天HH:mm:ss" />
        </div>
      </div>
      <div class="order-details">
        <p class="title">{{item.productName}}</p>
        <p class="detail">{{item.productTitle}}</p>
        <div class="detail-bottom">
          <div class="de-l">
            <div class="price">
              <p class="btm-title">预约价：</p>
              <p class="symbol">￥{{item.price | formatPrice}}</p>
            </div>
            <div class="ordered">
              <p class="btm-title">已预约：</p>
              <p class="men">{{item.reserveNum}}人</p>
            </div>
          </div>
          <div class="order-btn">立即预约</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    reverse: {
      type: Array,
      default: () => { return [] }
    }
  },
  data() {
    return {
    }
  },
  methods: {
    /**
     * @description 立即预约
     */
    goOrder(item) {
      if (this.$util.isSgRn()) {
        window.postMessage(
          JSON.stringify({
            type: 'goProduct',
            data: {
              id: item.productId
            }
          })
        )
      } else {
        this.$router.push(`/goodsDetail?productId=${item.productId}`)
      }
    }
  }
}
</script>
