<template>
  <div class="order-more-page">
    <!-- 头部 -->
    <Header v-if="!$route.meta.hideHeader" />
    <newOrder :reverse="list" />
  </div>
</template>
<script>
import Header from '@/components/header'
import newOrder from './components/new-order.vue' // 新品预约
export default {
  data() {
    return {
      list: []
    }
  },
  components: { Header, newOrder },
  beforeRouteEnter(from, to, next) {
    next(vm => {
      if (from.name === 'orderMore') {
        vm.list = vm.$util.strJSONParse(from.query.list, [])
      }
    })
  },
  activated() {
    document.documentElement.scrollTop = 0
  },
  methods: {

  }
}
</script>
